import React from 'react';
import { getStorage, ref, getDownloadURL } from "firebase/storage";
import { useState,useEffect } from 'react';
import {app} from '../utils/firebase-config';



const handleclick = (e) => {
    // console.log(e.target.currentSrc)
    window.open(e.target.currentSrc, '_blank', 'noopener,noreferrer', <link rel="icon" href="./media/ico_chef_black.ico" />)
}



const Caroussel3drotation = (props) => {

const [listimage, setlistimage] = useState([])

   useEffect(() => {
    for (let index = 0; index < props.explain_file.length; index++) {
        const element = props.explain_file[index];
               // Create a reference to the file we want to download
               const storage = getStorage(app);
               const starsRef = ref(storage,element);
               
               // Get the download URL
               getDownloadURL(starsRef)
                 .then((url) => {
                   // Insert url into an <img> tag to "download"
                   // console.log(url);
                   setlistimage(listimage=>[...listimage,url]);
                  //  console.log(url);
                 })
                 .catch((error) => {
                   // A full list of error codes is available at
                   // https://firebase.google.com/docs/storage/web/handle-errors
                   switch (error.code) {
                     case 'storage/object-not-found':
                       // File doesn't exist
                       break;
                     case 'storage/unauthorized':
                       // User doesn't have permission to access the object
                       break;
                     case 'storage/canceled':
                       // User canceled the upload
                       break;
               
                     // ...
               
                     case 'storage/unknown':
                       // Unknown error occurred, inspect the server response
                       break;
                        default:;
                   }
                 });
        
    }

       return () => {
        setlistimage([])
       }
   }, [props.explain_file])

    // const LoadImage= (imageurl)=>{
    
       
    
    //     // Create a reference to the file we want to download
    //     const storage = getStorage();
    //     const starsRef = ref(storage, imageurl);
        
    //     // Get the download URL
    //     getDownloadURL(starsRef)
    //       .then((url) => {
    //         // Insert url into an <img> tag to "download"
    //         // console.log(url);
    //         return url;
    //       })
    //       .catch((error) => {
    //         // A full list of error codes is available at
    //         // https://firebase.google.com/docs/storage/web/handle-errors
    //         switch (error.code) {
    //           case 'storage/object-not-found':
    //             // File doesn't exist
    //             break;
    //           case 'storage/unauthorized':
    //             // User doesn't have permission to access the object
    //             break;
    //           case 'storage/canceled':
    //             // User canceled the upload
    //             break;
        
    //           // ...
        
    //           case 'storage/unknown':
    //             // Unknown error occurred, inspect the server response
    //             break;
    //         }
    //       });
    
    // }



    return (
        <div>
            <div className="caroussel3drotation" style={{ ['--hei']: (100 / Math.tan(Math.PI / props.explain_file.length) + "px") }}>
                <div className="caroussel_content">
                    <div className="box"style={{['--a']: props.explain_file.length,['--hb']: props.height_box,['--wb']: props.width_box}}>
                        {/* {props.explain_file.map((element, index) =>
                            <span onClick={handleclick} key={element} style={{ ['--i']: index, ['--a']: props.explain_file.length, ['--per']: (50 / Math.tan(Math.PI / props.explain_file.length) + "px") }}><img src={element} alt={(element)} /></span>
                        )} */}
                        {listimage.map((element, index) =>
                            <span onClick={handleclick} key={element} style={{ ['--i']: index, ['--a']: props.explain_file.length, ['--per']: (50 / Math.tan(Math.PI / props.explain_file.length) + "px") }}><img src={element} alt={(element)} /></span>
                        )}

                        {/* <span style={{['--i']:"1"}}><img src="/media/000ball1.jpg" alt="" /></span>
                        <span style={{['--i']:"2"}}><img src="/media/000ball2.jpg" alt="" /></span>
                        <span style={{['--i']:"3"}}><img src="/media/000ball3.jpg" alt="" /></span>
                        <span style={{['--i']:"4"}}><img src="/media/000ball4.jpg" alt="" /></span>
                        <span style={{['--i']:"5"}}><img src="/media/000ball5.jpg" alt="" /></span>
                        <span style={{['--i']:"6"}}><img src="/media/000ball6.jpg" alt="" /></span>
                        <span style={{['--i']:"7"}}><img src="/media/000ball7.jpg" alt="" /></span>
                        <span style={{['--i']:"8"}}><img src="/media/000ball8.jpg" alt="" /></span> */}
                    </div>
                </div>
            </div>
        </div>
    );
};


export default Caroussel3drotation;