import React from 'react';

import { useState ,useEffect} from 'react';
import BiggerCard from './BiggerCard';
import Slide from './Slide';


import { getStorage, ref, getDownloadURL } from "firebase/storage";
import {app} from '../utils/firebase-config';

const Card = ({ id,
    cote,
    title,
    path,
    file,
    text,
    explain_title,
    explain_type,
    explain_file,
    explain_text,
    function_row }) => {
    // console.log(file[0])
// const [bigger, setbigger] = useState(false);
let bigger=false;

const [idnunber, setidnunber] = useState(id);
const [CardEnter, setCardEnter] = useState(false);



const [listimage, setlistimage] = useState([])

   useEffect(() => {
    for (let index = 0; index < file.length; index++) {
        const element = file[index];
               // Create a reference to the file we want to download
               const storage = getStorage(app);
               const starsRef = ref(storage,element);
               
               // Get the download URL
               getDownloadURL(starsRef)
                 .then((url) => {
                   // Insert url into an <img> tag to "download"
                   // console.log(url);
                   setlistimage(listimage=>[...listimage,url]);
                  //  console.log(url);
                 })
                 .catch((error) => {
                   // A full list of error codes is available at
                   // https://firebase.google.com/docs/storage/web/handle-errors
                   switch (error.code) {
                     case 'storage/object-not-found':
                       // File doesn't exist
                       break;
                     case 'storage/unauthorized':
                       // User doesn't have permission to access the object
                       break;
                     case 'storage/canceled':
                       // User canceled the upload
                       break;
               
                     // ...
               
                     case 'storage/unknown':
                       // Unknown error occurred, inspect the server response
                       break;
                        default:;
                   }
                 });
        
    }

       return () => {
        setlistimage([])
       }
   }, [])




const handleClick=()=>{
// setbigger(!bigger);
// setbigger(true);
bigger=true;


function_row(title,bigger,idnunber);

}
const MouseEnter= ()=>{
setCardEnter(true)
};
const MouseLeave= ()=>{
    setCardEnter(false)
    };

    return (
        <div className='card'onClick={handleClick} onMouseEnter={MouseEnter} onMouseLeave={MouseLeave}>
           
            

            <div className='picture'>
                {listimage.length === 1 ? <img key={listimage[0]} src={listimage[0]} alt={listimage[0]}></img> : <> <Slide list={listimage}></Slide></>}
                {/* {file.length === 1 ? <img key={file[0]} src={file[0]} alt={file[0]}></img> : <> <Slide list={file}></Slide></>} */}
            </div>
            <div className='card_title'>
                {CardEnter?<h1 className='hover'>{title}</h1>:<h1>{title}</h1>}
            </div>
            <div className="text">
                <p>{text}</p>
                
            </div>
          
{/* {bigger && <BiggerCard   
id={id}
cote={cote}
title={title}
path={path}
file={file}
text={text}
explain_title={explain_title}
explain_type={explain_type}
explain_file={explain_file}
explain_text={explain_text}

function_return={handleClick}
></BiggerCard>} */}
           
            


        </div>
    );
};

export default Card;