import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';

const Slide = ({list}) => {
   const [index, setindex] = useState(0)
//    const [timeup, settimeup] = useState(0)


useEffect(() => {
  const timer=setInterval(() => {
      if (index!==list.length-1) {
          setindex(index+1);
        
         
 
      }else{
        setindex(0) 
      }
  }, 6000);

  return () => {
    clearInterval(timer)
    
  }
})




    return (
        <div className='slide'>
           
            <img className='first' key={list[index]} src={list[index]} alt={list[index]} ></img>
           {index+1>=list.length? <img className='second' key={list[0]} src={list[0]} alt={list[0]}></img>:<img className='second' key={list[index+1]} src={list[index+1]} alt={list[index+1]}></img>} 
        </div>
    );
};

export default Slide;