
import { useState } from 'react';
import { useEffect } from 'react';
import Container from './components/Container';
import Footer from './components/Footer';
import Header from './components/Header';
import './style.scss';
import video from '../src/media/first_page.mp4';

function App() {
  const [bigger2, setbigger2] = useState(false);
  const [title_header, settitle_header] = useState();
  const [titlecard_header, settitlecard_header] = useState();
  const [compositioncard, setcompositioncard] = useState([]);
  const [idcard, setidcard] = useState();
  const [rownumber, setrownumber] = useState('');
  const [splashvisible, setsplashvisible] = useState(true);

  const bigger_function_general = (title, titlecard, bigger1, composition, idcard) => {
    setbigger2(bigger1);
    settitle_header(title);
    settitlecard_header(titlecard);
    setcompositioncard(composition);
    setidcard(idcard);
    // console.log(title,titlecard);
    // console.log(bigger2);
    // console.log(composition);
    // console.log(idcard);

  };
 const timer = () => {
      setInterval(() => {
        setsplashvisible(false)
      },48000)
    };

  useEffect(() => {
if (window.innerWidth>500) {
 
    timer();
    return () => {
      clearInterval(timer)
    }
}else{
  setsplashvisible(false)
}
    

  }
  )




  const function_row = (num) => {
    setrownumber(num);
    setbigger2(false);
  }
  const returnheader = () => {
    setbigger2(false);
    setrownumber('');
  }
const reset_timer=()=>{
  clearInterval(timer)
  setsplashvisible(false)
}


  return (



    <div className="App">


      {splashvisible ? <div className="screensplash"onClick={reset_timer} >
        <video autoPlay muted  >
          <source src={video} type="video/mp4" />
        </video>
        <div className='star1'></div>
        <div className='star2'></div>
        <div className='star3'></div>
        <div className='star4'></div>





      </div> : <>
        <Header bigger={bigger2} title={title_header} titlecard={titlecard_header} composition={compositioncard} idnumber={idcard} functionreturnheader={returnheader} row_explain={rownumber}></Header>
        {/* <Header title={title_header} titlecard={titlecard_header} composition={compositioncard} idnumber={idcard}></Header>  */}
        <Container bigger_function_general={bigger_function_general} function_row={function_row}></Container>
        {/* <Footer></Footer> */}
      </>
      }






    </div>
  );
}

export default App;
