import React from 'react';
import { useState } from 'react';
import Card from './Card';

const Row = ({bigger_function1,function_row, title, composition }) => {
    // const [bigger1, setbigger1] = useState(true);
   
    // let bigger1=false;
   
    
const bigger_function=(titlecard,bigger,idcard)=>{
    
    // setbigger1(!bigger1);
    // bigger1=true;
    bigger_function1(title,titlecard,bigger,composition,idcard);
    // console.log(title,titlecard,bigger1);
};
const handleClick=()=>{
    
  function_row(title) ;
 
};

    return (
        <div className='rows'>
            <h1 onClick={handleClick}>{title}</h1>
            <div className='row'>
                {composition.map((element, index) => <Card

                    key={index}

                    id={element.id}
                    cote={element.cote}
                    title={element.title}
                    path={element.path}
                    file={element.file}
                    text={element.text}
                    explain_title={element.explain_title}
                    explain_type={element.explain_type}
                    explain_file={element.explain_file}
                    explain_text={element.explain_text}
                    function_row={bigger_function}
                ></Card>

                )}
            </div>

        </div>
    );
};

export default Row;