import {useState,useEffect}  from 'react';
import React from 'react';

const Titl = (props) => {

const [spantitle,setspantitle]=useState([]);
// console.log(props.index)
const [ind,setind]=useState([]);




useEffect(() => {


       


const tableau_letters=[];
  
  for (let index = 0; index < props.title.length; index++) {
      const element = props.title[index];
      tableau_letters.push(element);
  }    
  
setspantitle(tableau_letters);




}, [props.title]);

useEffect(() => {
    setind((props.index*64)+"px")
    return () => {
        
    }
}, [props.index])








    return (
        
       < >

       
            <div className="span-container s1 " key="c1">

                {spantitle.map((element,index)=>{
                                    let styl=index*0.3 +"s";
                                    
                                        return(
                                        <span key={"sp1"+index} style={{transitionDelay:styl}} >{element}</span>
                                    )
                              
                                    }
                                )
                                }

                </div>
                <div className="span-container s2" key="c2">

                    {spantitle.map((element,index)=>{
                                let styl=index*0.03 +"s";
                             
                                    return(
                                    <span key={"sp2"+index} style={{transitionDelay:styl, top:ind}} >{element}</span>
                                )
                           
                                }
                            )
                            }

                </div>
      
            </>









    );
};

export default Titl;