import React, { useEffect, useState } from 'react';
import Titl from './Titl';


const Wordtitle = ({title}) => {

    const [list_title,setlist_title]=useState([])


    useEffect(() => {

    setlist_title(title.split(" "));
    
    }, [title])





    return (
        
        list_title.map((element,index)=>{
            return(
                
            // <div key={"w11"+index}>     
            // <div className="title">
                 <Titl  title={element} index={index} key={"w1"+index}/>
            // </div>    
           
            // </div>
        
        )
       
        }
       
        )
        
    );
        
         
     
              



          

         
            
            
            
        
    
   
};

export default Wordtitle;